import React from "react";
import logo from "./logo.svg";
import "./App.css";
import Container from "@mui/material/Container";
import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import Input from "@mui/material/Input";
import ListItemButton from "@mui/material/ListItemButton";
import ListItemIcon from "@mui/material/ListItemIcon";
import Icon from "@mui/material/Icon";
import ListItemText from "@mui/material/ListItemText";
import CircularProgress from "@mui/material/CircularProgress";
import Collapse from "@mui/material/Collapse";
import List from "@mui/material/List";
import ReactJson from "react-json-view";
import { Divider, Typography } from "@mui/material";
import Axios from "axios";

const BASEURL = "https://pdf-to-json-v1.herokuapp.com";

function FilePreview({ item, removeHandle, pdfJson, currentStatus }) {
  const [open, setOpen] = React.useState(false);
  const handleClick = () => {
    setOpen(!open);
  };
  const status = ["Queue", "Proccessing", "Finished", "Failed"];
  return (
    <div style={{ backgroundColor: "ActiveBorder" }}>
      <div style={{ display: "flex" }}>
        <ListItemButton
          onClick={handleClick}
          style={{
            flex: 1,
            justifyItems: "center",
            justifyContent: "space-between",
          }}
        >
          <div
            style={{
              flex: 2,
              display: "flex",
              flexDirection: "row",
              alignItems: "center",
            }}
          >
            <ListItemIcon>
              <Icon>picture_as_pdf</Icon>
            </ListItemIcon>
            <ListItemText primary={item.name} />
          </div>
          <div
            style={{
              flex: 1,
              display: "flex",
              flexDirection: "row",
              alignItems: "center",
              justifyContent: "flex-end",
            }}
          >
            <ListItemText
              primary={status[currentStatus]}
              style={{
                alignItems: "center",
                justifyContent: "center",
                display: "flex",
                color:
                  currentStatus == 0
                    ? "yellowgreen"
                    : currentStatus == 2
                    ? "green"
                    : currentStatus == 3
                    ? "red"
                    : "GrayText",
                size: 10,
              }}
            />
            {/* {open ? <ExpandLess /> : <ExpandMore />} */}
            {currentStatus == 2 && (
              <ListItemIcon>
                <Icon>expand_{open ? "less" : "more"}</Icon>
              </ListItemIcon>
            )}
            {currentStatus == 1 && <CircularProgress value={100} />}
          </div>
        </ListItemButton>
        <ListItemButton
          onClick={removeHandle}
          sx={{ flex: 0, alignItems: "center", justifyContent: "center" }}
        >
          <ListItemIcon>
            <Icon>delete</Icon>
          </ListItemIcon>
        </ListItemButton>
      </div>
      {currentStatus == 2 && (
        <Collapse in={open} timeout="auto" unmountOnExit>
          <List component="div" disablePadding>
            <div style={{ padding: 20 }}>
              <ReactJson
                displayDataTypes={false}
                theme={"monokai"}
                src={pdfJson["data"]}
                collapsed={true}
              />
            </div>
          </List>
        </Collapse>
      )}
    </div>
  );
}

function App() {
  const [FileLists, setFileLists] = React.useState([]);

  const removeFile = (obj) => {
    setFileLists(FileLists.filter((file) => file.file.name !== obj.name));
  };

  console.log(FileLists);

  const uploadFile = () => {
    FileLists.forEach((file) => {
      const item = FileLists.find((it) => it.file.name == file.file.name);
      item.currentStatus = 1;
      setFileLists([
        ...FileLists.filter(({ file: F }) => F.name !== file.file.name),
        item,
      ]);
      const formData = new FormData();
      formData.append("pdf", file.file);
      Axios.post(BASEURL + "/getjson", formData, {
        // onUploadProgress: (ev) => {
        //   console.log(ev);
        // },
        headers: { "Content-Type": "multipart/form-data" },
      })
        .then((data) => {
          const item = FileLists.find((it) => it.file.name == file.file.name);
          item.pdfJson = data.data;
          item.currentStatus = 2;
          setFileLists([
            ...FileLists.filter(({ file: F }) => F.name !== file.file.name),
            item,
          ]);
        })
        .catch((err) => {
          const item = FileLists.find((it) => it.file.name == file.file.name);
          item.pdfJson = {};
          item.currentStatus = 3;
          setFileLists([
            ...FileLists.filter(({ file: F }) => F.name !== file.file.name),
            item,
          ]);
          console.log("Error: ", err);
        });
    });
  };

  return (
    <Container component="main" style={{ width: "100%" }}>
      <Box
        sx={{
          marginTop: 8,
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
        }}
      >
        <Box component="form" noValidate sx={{ mt: 1, width: "100%" }}>
          <div
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
            }}
          >
            <Typography sx={{ fontSize: 20 }}>
              PDFs to JSON Converter
              <Typography sx={{ fontSize: 12 }}>
                made by @omaribnmizan
              </Typography>
            </Typography>
            <Button
              type="button"
              fullWidth
              variant="outlined"
              sx={{
                mt: 3,
                mb: 2,
                ml: "auto",
                width: "30%",
                alignSelf: "flex-end",
              }}
            >
              <input
                multiple
                onChange={({ target: { files } }) => {
                  setFileLists(
                    new Array(files.length).fill(false).map((i, ind) => ({
                      currentStatus: 0,
                      pdfJson: {},
                      file: files.item(ind),
                    }))
                  );
                }}
                accept="application/pdf"
                type="file"
                style={{ opacity: 0, position: "absolute", width: "100%" }}
              />
              Upload PDFs
            </Button>
          </div>
          <Divider />
          {FileLists.map((file) => (
            <FilePreview
              removeHandle={() => removeFile(file.file)}
              key={file.file.name}
              item={file.file}
              pdfJson={file.pdfJson}
              currentStatus={file.currentStatus}
            />
          ))}
          <Divider />
          <div
            style={{
              backgroundColor: "#fff",
              position: "sticky",
              bottom: 0,
            }}
          >
            <Button
              disabled={FileLists.length === 0}
              onClick={uploadFile}
              fullWidth
              variant="contained"
              sx={{
                mt: 3,
                mb: 2,
                width: "30%",
              }}
            >
              Convert to JSON
            </Button>
          </div>
        </Box>
      </Box>
    </Container>
  );
}

export default App;
